* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: bisque;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-form {
  font: inherit;
  justify-content: center;
  margin-bottom: 16px;
  padding: 20px;
  display: flex;
}

input, button {
  padding: 10px;
  font-family: inherit;
}

ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

img {
  display: block;
}

.gallery {
  width: 1200px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  display: flex;
}

.photo-card {
  padding: 10px;
  display: block;
}

.photo-img {
  width: 175px;
  height: 175px;
  object-fit: cover;
  border-radius: 5px;
}

.info-item {
  margin: 0;
  font-family: inherit;
}

.load-more {
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.search-input, .search-btn, .load-more {
  border: none;
  border-radius: 5px;
}

.search-btn {
  margin-left: 10px;
}

.search-btn:hover, .load-more:hover {
  background-color: rgba(127, 255, 246, .14);
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

